'use strict';

/**
 * Handles loading the user for the application
 */
window.App.ApplicationRoute = Ember.Route.extend(window.App.Apikey, {
  model: function(params) {
    var apikey = this.getApikey();

    if (apikey) {
      return Ember.$.ajax({
        type: 'GET',
        url: 'https://api.byfirebepurged.com/user',
        headers: {
          Authorization: 'apikey ' + apikey
        },
        contentType:'application/json'
      });
    } else {
      return undefined;
    }
  },

  actions: {
    loadUser: function() {
      this.refresh();
    }
  }
});
