'use strict';

window.App.RaidsIndexRoute = Ember.Route.extend(window.App.Apikey, {
  model: function() {
    var apikey = this.getApikey();

    if (apikey) {
      return Ember.$.ajax({
        type: 'GET',
        url: 'https://api.byfirebepurged.com/raids',
        headers: {
          Authorization: 'apikey ' + apikey
        },
        contentType:'application/json'
      });
    } else {
      return this.transitionTo('index');
    }
  }
});
