'use strict';

/**
 * Controller for the login/logout view
 */
window.App.AuthController = Ember.Controller.extend(window.App.Apikey, {
  needs: ['application'],

  user: Ember.computed.alias('controllers.application.user'),

  actions: {
    login: function() {
      $.ajax({
        type: 'POST',
        url: 'https://api.byfirebepurged.com/login',
        data:JSON.stringify({
          redirect: window.location.protocol + '//' +
            window.location.host + '/#/apikey/'
        }),
        contentType:'application/json',
        success: function(data) {
          window.location = data.href;
        }
      });
    },

    logout: function() {
      this.removeApikey();
      this.set('user', null);
      this.transitionToRoute('index');
    }
  }
});
